.app *,
.app *::before,
.app *::after {
  box-sizing: border-box;
}
.app .spinner-border {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.app .search {
  width: 300px;
  margin: 40px auto;
}
.app .add-new {
  display: block;
  margin: 0 auto;
}
.app .name {
  margin: 25px auto;
}
.app .name .item {
  display: flex;
  width: 340px;
  margin: 5px auto;
  margin-bottom: 0;
}
.app .name .item span {
  margin: 5px;
}
.app .name .item .note-name {
  width: 100%;
  font-size: 16px;
  margin-bottom: 10px;
}
.app .name .item .note-btn {
  width: 50px;
  margin-left: 5px;
  margin-bottom: 10px;
}
.app .name .item button .fa {
  font-size: 1.5em;
}
.app .name .tags {
  display: flex;
  width: 340px;
  margin: 5px auto;
  margin-bottom: 0;
  flex-wrap: wrap;
}
.app .name .tags span {
  margin: 5px;
}
.app .name .tags div {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  margin: 5px;
  padding: 0 5px;
  font-size: 1em;
  border: solid 1px #5bc0de;
  border-radius: 5px;
}
.app .name .tags div button {
  height: 20px;
  width: 17px;
  padding: 0;
  vertical-align: middle;
  border: transparent;
}
.app .name .tags div button .fa {
  margin: 0;
  font-size: 1em;
}
.app .name .tags button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  margin: 5px;
  padding: 0 5px;
  font-size: 1em;
}
.app .modal .modal-content {
  margin: 20px auto;
  width: 340px;
  border: solid 2px black;
  border-radius: 5px;
}
.app .modal.show {
  display: block;
  top: 20%;
}
.app .modal.show input {
  margin: 10px auto;
  width: 300px;
}
.app .modal.show .container {
  width: 300px;
  height: 180px;
  display: block;
  margin-left: 3px;
  border-radius: 5px;
}
.app .modal.show .container .backdrop {
  width: 300px;
  height: 180px;
  position: absolute;
  z-index: 1;
  border: 2px solid #685972;
  background-color: #ffffff;
  overflow: auto;
  pointer-events: none;
  transition: transform 1s;
}
.app .modal.show .container .backdrop .highlights {
  font: 16px 'Open Sans', sans-serif;
  letter-spacing: 1px;
  white-space: pre-wrap;
  word-wrap: break-word;
  color: transparent;
  padding: 10px;
}
.app .modal.show .container .backdrop .highlights mark {
  font: 16px 'Open Sans', sans-serif;
  letter-spacing: 1px;
  padding: 0;
  border-radius: 3px;
  background-color: #b1d5e5;
  color: transparent;
}
.app .modal.show .container textarea {
  width: 300px;
  height: 180px;
  font: 16px 'Open Sans', sans-serif;
  letter-spacing: 1px;
  position: absolute;
  z-index: 2;
  margin: 0;
  padding: 10px;
  border: 2px solid #74637f;
  border-radius: 0;
  color: #444444;
  background-color: transparent;
  overflow: auto;
  resize: none;
  transition: transform 1s;
}
